import React, { Component } from "react";
import { Button } from "react-bootstrap";

import firebase from "./firebase";

//css

import "./Style/feeback.css";

class feedback extends Component {
  constructor() {
    super();
    this.state = {
        email:"",
        feedback:"",
      showPerson: false,
    };
  }
  togglePersonHandler = () => {
    const isVisible = this.state.showPerson;
    this.setState({
      showPerson: !isVisible,
    });
  };

  handleText = (e) =>{
      this.setState({
          [e.target.name]:e.target.value
      },console.log(e.target.value))
  }
  sendFeed = () => {
    
    const db = firebase.firestore();
    db.collection("feedbacker").add({
      email:this.state.email,
      feedback:this.state.feedback
    })
    .then(function(){
      console.log("document send")
      window.location="."
    })
    .catch(function(err){
      console.log("something went wrong@@@@")
    })
    
  }
  render() {
    let feedback = null;
    if (this.state.showPerson) {
      feedback = (
        <div className="feedback-card">
          <label className="ma2">Email (optional)</label>
          <input
            name="email"
            value={this.state.email}
            type="email"
            placeholder="Email"
            onChange={this.handleText}
          />
          <br></br>
          <label className="ma2">Feedback</label>
          <input
            name="feedback"
            type="text"
            value={this.state.feedback}
            placeholder="What can i improve"
            onChange={this.handleText}
            className="feedback-input"
          />
          <Button className="btn1"  onClick={this.sendFeed}>
              Send 
          </Button>
        </div>
      );
    }
    return (
      <div>
        <p className="btn" onClick={this.togglePersonHandler}>
          Click for give a feedback
        </p>
        <div
          className="container"
          style={{ display: "flex", justifyContent: "center" }}
        >
          {feedback}
        </div>
      </div>
    );
  }
}

export default feedback;
